@import '~antd/dist/antd.css';
:root {
    --side-width: 20vw;
    --shadow-slight: 0 0 1px rgb(0 0 0 / 15%);
    --my-header-row-height: 32px;
    --my-header-color: #5a6c84;
    /* --my-header-bgcolor: hsl(213, 26%, 93%); */
    --my-header-bgcolor: hsl(226, 100%, 95%);
    --my-cell-padding: 8px 12px;
    --my-font-size: 12px;
    --my-line-height: 1.28571;
    --my-header-cell-border-horizontal: var(--header-cell-border);
    --my-header-cell-border-vertical: var(--header-cell-border);
}

.MyApp {
    display: flex;
    flex-direction: column;
    background-color: hsla(98, 100%, 90%, 0.8);
}

.side-bar {
    background-color: hsl(0, 0%, 100%);
    position: fixed;
    display: flex;
    width: var(--side-width);
    flex-direction: column;
    min-height: 100vh;
    font-size: calc(1vw + 1em);
    box-shadow: var(--shadow-slight);
}

.side-bar header {
    margin: 0 auto;
}

.side-bar footer {
    font-size: 0.7em;
    margin-left: 1vw;
}

.side-nav {
    display: block;
    list-style: none;
    padding-left: 0;
}

.side-nav a {
    text-decoration: none;
    color: black;
}

.side-item {
    cursor: pointer;
    font-weight: 500;
    text-align: center;
}

.side-item:hover {
    color: hsl(220, 100%, 70%);
    background-color: hsl(209, 80%, 95%);
    border-radius: 30px;
}

a.side-item-hover {
    color: hsl(220, 100%, 70%);
}

.site-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    font-size: calc(1vw + 0.6em);
    padding-left: calc(var(--side-width) + 2vw);
}

.site-content header {
    justify-self: start;
}

.site-footer {
    font-size: small;
    color: hsl(212, 100%, 75%);
    padding-left: calc(var(--side-width) + 2vw);
    margin-bottom: 2vh;
}

.uploader {
    background-color: hsla(0, 0%, 80%, 0.9);
    height: 30vh;
    width: 40vw;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: var(--shadow-slight);
    text-align: center;
    margin-top: 2vw;
    border-radius: 5px;
    cursor: pointer;
}

.uploader-content {
    background-color: hsl(0, 0%, 90%);
    height: 90%;
    width: 90%;
    display: flex;
    flex-direction: column;
    padding: 4px;
    color: hsl(0, 0%, 70%);
    border-radius: 5px;
    box-shadow: var(--shadow-slight);
}

.uploader-content span {
    font-size: 0.5em;
}

.add_icon {
    transform: scale(0.5);
}

input[type="file"] {
    display: none;
}

.page {
    width: 30vw;
    margin-right: 2vw;
    margin-top: 2vh;
    background-color: rgb(255, 255, 255);
    box-shadow: var(--shadow-slight);
}

.tinyImg {
    width: 20vw;
    margin-right: 2vw;
    margin-top: 2vh;
    box-shadow: var(--shadow-slight);
}

.chart-show {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: hsla(98, 100%, 90%, 0.8);
    min-height: 100vh;
    height: 100vh;
    left: var(--side-width);
    right: 0;
    top: 50vh;
    bottom: 0;
}

.chart-show-button {
    width: 5vw;
}

.ali_table {
    margin-right: 2vw;
    margin-top: 3vh;
    overflow: auto;
    max-height: 80vh;
    max-width: 70vw;
}


.alitable_multisele_cols {
    display: table-row;
    position: fixed;
    top: 0;
    left: calc(var(--side-width) + 6vw);
    right: 2vw;
    z-index: 99;
    overflow-x: auto;
    overflow-y: hidden;
    background: var(--my-header-bgcolor);
    font-size: var(--my-font-size);
    line-height: var(--my-line-height);
    vertical-align: middle;
    border-collapse: collapse;
}

.alitable_multisele_cols_cell {
    display: inline;
    font-weight: bold;
    font-size: var(--my-font-size);
    line-height: var(--my-line-height);
    text-align: left;
    vertical-align: middle;
    padding: var(--my-cell-padding);
    height: var(--my-header-row-height);
    color: var(--my-header-color);
    background: var(--my-header-bgcolor);
    /* border: none; */
    border: 1px solid #dfe3e8;
    border-right: var(--my-header-cell-border-vertical);
    border-bottom: var(--my-header-cell-border-horizontal);
}

.alitable_multisele_cols_cell div {
    display: table-cell;
    vertical-align: middle;
}

.ali_table_send_button {
    position: fixed;
    top: 1vh;
    left: calc(var(--side-width) + 1vw);
    z-index: 100;
}

.ali_table_alert {
    position: absolute;
    opacity: 1;
    top: 30vh;
    left: 0;
    z-index: 100;
    transition: all 0.5s;
}

/* 站长统计代码 */
body > a {
    position: absolute;
    z-index: 2;
    bottom: 0.1vw;
    left: calc(var(--side-width) + 0.5vw);
}